
.ui-button {
  text-decoration: none;
  color: #000;
  background: #fff;
  padding: 15px 30px;
  letter-spacing: 0.3em;
  font-size: 12px;
  font-weight: bolder;
  -webkit-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
  border: 2px solid #000;
  display: inline-block;
}
.ui-button:hover {
  color: #fff;
  background: #000;
}



